<template>
  <div class="app-container">
    <van-tabs
      v-model="activeName"
      :before-change="moreState"
      title-inactive-color="rgb(255,255,255,0.6)"
      title-active-color="#fff"
      line-width="65px"
      color="#ffcc7c"
      background="#95c9ff"
      animated
    >
      <van-tab title="1.填写账号">
        <van-cell
          is-link
          title="请选择找回方式"
          :value="actionVal"
          @click="show = true"
        />
        <phone-form @next="next" v-show="actionVal == `手机号找回`" />
        <email-form @next="next" v-show="actionVal == `邮箱找回`" />
      </van-tab>
      <van-tab title="2.重置密码">
        <password-form @next="next" :loadingState="loadingState"
      /></van-tab>
      <van-tab title="3.设置完成">
        <div class="success_wrapper">
          <div class="text-wrpper">
            <div class="icon-wrapper">
              <i class="el-icon-success i_success" v-if="success"></i>
              <i class="el-icon-error i_err" v-else></i>
            </div>
            <div class="text" v-if="success">您的密码已设置成功</div>
            <div class="text" v-else>很遗憾，密码重置失败</div>
          </div>
          <el-button
            type="primary"
            class="form-button"
            @click="removeLocalStorage"
            >返回</el-button
          >
        </div></van-tab
      >
    </van-tabs>
    <van-action-sheet
      v-model="show"
      :actions="actions"
      cancel-text="取消"
      close-on-click-action
      @select="onSelect"
      @cancel="onCancel"
    />
  </div>
</template>

<script>
import { PhoneForm, EmailForm, PasswordForm } from "./components";
import { setEncryption } from "@/mixins/user";
import { resertPass } from "@/api/user";
export default {
  components: { PhoneForm, EmailForm, PasswordForm },
  mixins: [setEncryption],
  props: {},
  data() {
    return {
      success: false,
      show: false,
      actions: [
        { name: "手机号找回" },
        // { name: "人工找回" },
        // { name: "邮箱找回" },
      ],
      state: false,
      loadingState: false,
      activeName: Number(localStorage.getItem("reset_index")) || 0,
      actionVal: "手机号找回",
    };
  },
  watch: {},
  computed: {},
  methods: {
    removeLocalStorage() {
      localStorage.removeItem("reset_index");
      localStorage.removeItem("reset_phone");
      localStorage.removeItem("reset_code");
      localStorage.removeItem("reset_password");
      this.$router.go(-1);
    },
    onSelect(item) {
      console.log(item.name);
      this.show = false;
      this.actionVal = item.name;
    },
    onCancel() {
      this.$toast("取消");
    },
    moreState(index) {
      return new Promise((resole, reject) => {
        if (this.state) {
          this.state = false;
          resole();
        }
        reject();
      });
    },
    next(props) {
      for (const key in props) {
        let object = JSON.stringify(props);
        localStorage.setItem(key, object);
      }
      let activeName = this.activeName;
      if (activeName !== 1) {
        activeName++;
        this.activeName = activeName;
        this.state = true;
      } else {
        this.loadingState = true;
        resertPass({
          phone_num: JSON.parse(localStorage.getItem("reset_phone"))
            .reset_phone,
          password: this.encryption(
            JSON.parse(localStorage.getItem("reset_password")).reset_password
          ),
          code: JSON.parse(localStorage.getItem("reset_code")).reset_code,
        }).then((res) => {
          // console.log(res);
          if (res.status_code === 1) {
            this.success = true;
            this.loadingState = false;
            activeName++;
            this.activeName = activeName;
            this.state = true;
          }
        });
      }
      if (activeName < 2) {
        localStorage.setItem("reset_index", activeName);
      }
    },
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
@import "../../../../styles/global";
.app-container {
  .van-cell {
    margin-bottom: 25px;
  }
  .success_wrapper {
    width: 300px;
    height: auto;
    margin: 100px auto 0;
    position: relative;
    .text-wrpper {
      @include center;
      .icon-wrapper {
        margin-right: 20px;
        i {
          font-size: 25px;
        }
        .i_success {
          color: #e5b467;
        }
        .i_err {
          color: #f35858;
        }
      }
      .text {
        font-size: 18px;
        padding-left: 10px;
      }
    }
    .form-button {
      margin-top: 80px;
      width: 100%;
      height: 45px;
      font-size: 18px;
      text-indent: 4px;
      letter-spacing: 4px;
      background-color: #73befb;
      border-color: #73befb;
      border-radius: 40px;
      &:hover {
        opacity: 0.8;
      }
      &:active {
        opacity: 0.8;
      }
    }
  }
}
</style>


