<template>
  <div class="app-container">
    <form action ref="form-wrapper" class="form-wrapper">
      <div class="input-wrapper">
        <div class="label">手机号</div>
        <div class="text-input">
          <input
            type="text"
            v-model="loginForm.phone"
            placeholder="请输入手机号"
          />
        </div>
      </div>
      <div class="input-wrapper">
        <div class="label">图形码</div>
        <div class="text-input">
          <input
            type="text"
            v-model="loginForm.sidentify"
            placeholder="请输入图形码的答案"
          />
        </div>
        <div class="code" @click="refreshCode">
          <van-image width="100%" height="100%" lazy-load :src="refresh.b64s" />
        </div>
      </div>
      <div class="input-wrapper">
        <div class="label">验证码</div>
        <div class="text-input">
          <input
            type="text"
            v-model="loginForm.code"
            placeholder="请输入验证码"
          />
        </div>
        <div class="getValid">
          <span v-show="show" @click="getCode">获取验证码</span>
          <span v-show="!show" class="count">{{ count }}s后重新发送</span>
        </div>
      </div>
      <div class="submit-wrapper">
        <button type="submit" @click.prevent="onSubmit" class="submit">
          下一步
        </button>
      </div>
    </form>
  </div>
</template>

<script>
import { artificial_code } from "@/mixins/user";
import { getCode as getCode_api, captcha } from "@/api/code";
import { isPhone,isSmsCode } from "@/utils/validate";
export default {
  mixins: [artificial_code],
  props: {},
  data() {
    return {
      refresh: {
        b64s: "",
        id: "",
      },
      show: true,
      count: "",
      timer: null,
      loginForm: {
        sidentify: "",
        code: "",
        phone: "",
      },
    };
  },
  watch: {},
  computed: {},
  methods: {
    onSubmit() {
      this.rules().then((valid) => {
        if (valid) {
          this.next();
        }
      });
    },
    next() {
      this.$emit("next", {
        reset_phone: this.loginForm.phone,
        reset_code: this.loginForm.code,
      });
    },
    async rules() {
      if (this.loginForm.phone !== "") {
        if (!isPhone(this.loginForm.phone)) {
          this.$toast("请填写正确手机号");
          return false;
        }
      } else {
        this.$toast("手机号不能为空");
        return false;
      }
      if (this.securityLevel !== 0) {
        if (this.loginForm.sidentify !== "") {
          let response = await this.valideCode();
          if (!response.data.verify_status) {
            this.refreshCode();
            this.$toast("请填写正确的图形码");
            return false;
          }
        } else {
          this.$toast("图形码不能为空");
          return false;
        }
      }
      if (this.loginForm.code !== "") {
        if (!isSmsCode(this.loginForm.code)) {
          this.$toast("请填写正确的验证码");
          return false;
        }
      } else {
        this.$toast("验证码不能为空");
        return false;
      }
      return true;
    },
    getCode() {
      let _this = this;
      if (isPhone(_this.loginForm.phone)) {
        _this.valideCode().then((res) => {
          if (res.data.verify_status) {
            _this.sendText();
          } else {
            _this.$toast("请先填写正确的图形码");
            return false;
          }
        });
      } else {
        _this.$toast("请先填写手机号");
        return false;
      }
    },
    async valideCode() {
      return await captcha(
        JSON.stringify({ id: this.refresh.id, value: this.loginForm.sidentify })
      );
    },
    sendText() {
      let _this = this;
      getCode_api(_this.loginForm.phone).then((response) => {
        this.$toast.success("发送成功");
        _this.countdown();
      });
    },
    countdown() {
      const TIME_COUNT = 60;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000);
      }
    },
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
@import "@/assets/styles/mobile/form.scss";
</style>


