<template>
  <form
    action
    ref="form-wrapper"
    style="padding-top: 20px"
    class="form-wrapper"
  >
    <div class="input-wrapper">
      <div class="label">密码</div>
      <div class="text-input">
        <input
          :type="pwdType"
          v-model="passwordForm.pass"
          placeholder="请设定登录密码"
        />
      </div>
      <div class="icon-right">
        <svg-icon icon-class="eye" @click="showPwd" />
      </div>
    </div>
    <div class="input-wrapper">
      <div class="label">确认密码</div>
      <div class="text-input">
        <input
          :type="checkPassType"
          v-model="passwordForm.checkPass"
          placeholder="请再输入一次登录密码"
        />
      </div>
      <div class="icon-right">
        <svg-icon icon-class="eye" @click="showcheckPass" />
      </div>
    </div>
    <div class="submit-wrapper">
      <van-button
        type="submit"
        :loading="loadingState"
        @click.prevent="updatePassword"
        class="submit"
        >下一步</van-button
      >
    </div>
  </form>
</template>

<script>
import { isPassWord } from "@/utils/validate";
export default {
  components: {},
  props: {
    loadingState: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      pwdType: "password",
      checkPassType: "password",
      passwordForm: {
        pass: "",
        checkPass: "",
      },
    };
  },
  watch: {},
  computed: {},
  methods: {
    async rules() {
      if (this.passwordForm.pass !== "") {
        if (!isPassWord(this.passwordForm.pass)) {
          this.$toast("请输入至少6位");
          return false;
        }
      } else {
        this.$toast("请输入密码");
        return false;
      }
      if (!isPassWord(this.passwordForm.checkPass)) {
        this.$toast("请再次输入密码");
        return false;
      } else if (this.passwordForm.checkPass !== this.passwordForm.pass) {
        this.$toast("两次输入密码不一致!");
        return false;
      }
      return true;
    },
    updatePassword(event) {
      this.rules().then((valid) => {
        if (valid) {
          this.next();
        }
      });
    },
    next() {
      this.$emit("next", { reset_password: this.passwordForm.pass });
    },
    showPwd(event) {
      if (this.pwdType === "password") {
        this.pwdType = "";
      } else {
        this.pwdType = "password";
      }
    },
    showcheckPass() {
      if (this.checkPassType === "password") {
        this.checkPassType = "";
      } else {
        this.checkPassType = "password";
      }
    },
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
@import "@/assets/styles/mobile/form.scss";
.label {
  width: 4rem !important;
  // text-align: unset !important;
  // text-align-last: unset !important;
}
/deep/ .van-button--loading{
  pointer-events: none;
}
</style>


